/* HomePage.module.css */
.cardContainer {
   display: grid;
   grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
   gap: 60px;
   padding: 50px;
   background-color: #f6f6f6;
   margin-top: 50px;
   border-radius: 20px;
}

@media only screen and (max-width: 600px) {
   .cardContainer {
      padding: 0px;
      background-color: white;
      gap: 30px;
      margin-top: 30px;
   }
}
