/* Card.module.css */
.card {
   position: relative;
   overflow: hidden;
   height: 400px;
   border-radius: 10px;
   box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
   margin-bottom: 20px;
}

.cardImage {
   width: 100%;
   height: 100%;
   object-fit: cover;
}

.cardTitle {
   position: absolute;
   bottom: 0;
   color: white;
   width: 70%;
   height: 50px;
   font-size: 1.1em;
   padding-left: 15px;
   box-sizing: border-box;
}

@media only screen and (max-width: 600px) {
   .card {
      height: 300px;
      margin-bottom: 0px;
   }
}
