.logementContainer {
   position: relative;
}

.title {
   color: #ff6060;
   margin-bottom: 0px;
}

.location {
   color: #ff6060;
   font-size: 1.2em;
   font-weight: 600;
}

/*Tags*/

.tagsContainer {
   display: flex;
   flex-direction: row;
   align-items: center;
   padding: 0px;
   margin-top: 35px;
}

.tags {
   list-style-type: none;
   padding: 5px 30px 5px 30px;
   margin-right: 20px;
   border-radius: 15px;
   color: white;
   background-color: #ff6060;
}

/*Host*/

.hostContainer {
   position: absolute;
   top: 0px;
   right: 10px;
   display: flex;
   flex-direction: column;
}

.hostDef {
   display: flex;
}

.hostDef h2 {
   font-size: 1.2em;
   font-weight: normal;
   color: #ff6060;
   width: 85px;
   word-wrap: break-word;
}

.hostDef img {
   border-radius: 100%;
   width: 100px;
   height: 100px;
}

.rating {
   display: flex;
   justify-content: space-between;
   width: 100%;
   margin-top: 20px;
}

/* Collapse */

.collapseContainer {
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   width: 100%;
   margin-top: 30px;
}

.collapseDescription,
.collapseEquipments {
   width: 47%;
   border-radius: 15px;
}

.collapseDescription {
   color: #ff6060;
}

.equipments {
   list-style-type: none;
   color: #ff6060;
   margin-bottom: 5px;
}

.collapseUl,
.collapseP {
   padding-inline-start: 15px;
}

.collapseContent {
   min-height: 180px;
}

@media only screen and (max-width: 600px) {
   .logementContainer {
      position: static;
      display: flex;
      flex-direction: column;
   }

   .tagsContainer {
      margin-top: 10px;
      margin-bottom: 25px;
   }

   .tags {
      border-radius: 10px;
      margin-right: 10px;
      padding: 7px 15px 7px 15px;
      font-size: 0.9em;
   }

   .hostContainer {
      position: static;
      flex-direction: row-reverse;
      width: 100%;
      justify-content: space-between;
   }

   .hostDef img {
      width: 70px;
      height: 70px;
   }

   .hostDef h2 {
      font-size: 1em;
      width: 70px;
   }

   .rating {
      width: 40%;
      margin-inline-start: 0px;
   }

   .collapseContainer {
      flex-direction: column;
   }

   .collapseDescription,
   .collapseEquipments {
      width: 100%;
      margin-bottom: 30px;
   }

   .title {
      font-size: 1.4em;
   }

   .location {
      font-size: 0.9em;
   }
}
